  font-size: 1.5rem;
}

#resume {
  li {
    padding-bottom: .6em;
    list-style: square;
  }
}
.curtain {
  margin: 0 auto;
  width: 100%;
  height: $curtainHeight;
  max-height: 100vh;
  overflow: hidden;

  &__wrapper {
    width: 100%;
    height: 100%;

    input[type=checkbox] {
      position: absolute;
      cursor: pointer;
      width: 100%;
      height: 100%;
      z-index: 100;
      opacity: 0;
      top: 0;
      left: 0;
      // When the checkbox is checked...
      &:checked {
        // Slide the first panel
        & ~ div.curtain__panel--left {
          transform: translateX(0);
        }
        // Slide the second panel
        & ~ div.curtain__panel--right {
          transform: translateX(0);
        }
      }
    }
  }

  &__panel {
    display: flex;
    align-items: center;
    background: purple;
    color: #fff;
    float: left;
    position: relative;
    width: 50%;
    height: $curtainHeight;
    max-height: 100vh;
    transition: all 500ms linear;
    z-index: 2;

    &--left {
      justify-content: flex-end;
      transform: translateX(-100%);
    }

    &--right {
      justify-content: flex-start;
      transform: translateX(100%);
    }
  }

  &__content {
    //background: #333;
    //color: #fff;
    padding: 1rem 0;
    position: absolute;
    text-align: center;
    z-index: 1;
    width: 100%;
    height: $curtainHeight;

    img {
      width: 20%;
    }
  }
}